<template>
  <div class="garden-page">
    <div class="garden-background"></div>
    
    <div class="container">
      <div class="garden-header card">
        <h2 class="card-title">Ëä±ËØ≠Âùä</h2>
        <p class="garden-description">
          Âú®ËøôÈáåÔºå‰Ω†ÂèØ‰ª•Êü•ÁúãÂíåÁÆ°ÁêÜ‰Ω†ÁöÑÊ§çÁâ©ÔºåÊâìÈÄ†Â±û‰∫éËá™Â∑±ÁöÑËä±Âõ≠„ÄÇ
        </p>
        <div class="garden-stats">
          <div class="stats-item plants">
            <span class="stats-icon">üå±</span>
            <span class="stats-value">{{ myPlants.length }}</span>
            <span class="stats-label">Ê§çÁâ©</span>
          </div>
        </div>
      </div>
      
      <div class="garden-content">
        <div class="my-garden-section card">
          <div class="section-header">
            <h3 class="section-title">Êú™ÂÆåÊàêÁöÑÊò•Â§©</h3>
          </div>
          
          <div class="empty-garden" v-if="myPlants.length === 0">
            <el-empty description="‰Ω†ÁöÑËä±Âõ≠ËøòÁ©∫Á©∫Â¶Ç‰πü~">
              <template #image>
                <div class="empty-image">üè°</div>
              </template>
            </el-empty>
          </div>
          
          <div v-else class="garden-plants-row">
            <div 
              v-for="plant in filteredPlants" 
              :key="plant.id" 
              class="garden-plant-item"
              :class="{ 'is-main-plant': plant.isMainPlant }"
            >
              <div class="plant-avatar">
                <WeatherCanvas :weather="plant.weather || 'sunny'" :width="200" :height="200" />
                <!-- ‰ΩøÁî®ÂõæÁâáÊõø‰ª£emoji -->
                <img :src="getPlantImage(plant)" class="plant-image" alt="Ê§çÁâ©ÂõæÁâá" />
              </div>
              
              <div class="plant-details">
                <div class="plant-header">
                  <div class="plant-name">{{ plant.name }}</div>
                  <div v-if="plant.isMainPlant" class="main-plant-badge">
                    <el-tag size="small" type="success" effect="dark">‰∏ªË¶ÅÊ§çÁâ©</el-tag>
                  </div>
                </div>
                
                <div class="plant-level-container">
                  <div class="plant-level">Á≠âÁ∫ß: <span class="level-value">{{ plant.level }}</span></div>
                  <el-progress 
                    :percentage="calculatePlantExp(plant)" 
                    :format="expFormat" 
                    :stroke-width="10"
                    class="plant-exp-progress"
                  />
                </div>
                
                <!-- Ê∑ªÂä†Â§©Ê∞îÈÄâÊã©Âô® -->
                <div class="plant-weather-selector">
                  <span class="weather-label">ÁéØÂ¢É:</span>
                  <div class="weather-options">
                    <span 
                      class="weather-option" 
                      :class="{ active: plant.weather === 'sunny' || !plant.weather }"
                      title="Êô¥Â§©"
                      @click="updatePlantWeather(plant, 'sunny')"
                    >‚òÄÔ∏è</span>
                    <span 
                      class="weather-option" 
                      :class="{ active: plant.weather === 'rainy' }"
                      title="‰∏ãÈõ®"
                      @click="updatePlantWeather(plant, 'rainy')"
                    >üåßÔ∏è</span>
                    <span 
                      class="weather-option" 
                      :class="{ active: plant.weather === 'cloudy' }"
                      title="Â§ö‰∫ë"
                      @click="updatePlantWeather(plant, 'cloudy')"
                    >‚òÅÔ∏è</span>
                  </div>
                </div>
              </div>
              
              <div class="plant-actions">
                <el-button size="small" type="primary" @click="showDialog(plant)">
                  <span class="button-icon">üí¨</span>ËÅÜÂê¨ÂøÉÂ£∞
                </el-button>
                <el-button 
                  size="small" 
                  type="success" 
                  plain
                  @click="setAsMainPlant(plant)"
                  :disabled="plant.isMainPlant"
                >
                  <span class="button-icon">‚≠ê</span>ËÆæ‰∏∫‰∏ªÊ§çÁâ©
                </el-button>
              </div>
            </div>
          </div>
          
          <div class="plant-companionship" v-if="myPlants.length > 0">
            ‰∏çÁü•‰∏çËßâ‰∏≠ÔºåÊ§çÁâ©Â∑≤ÁªèÈô™‰º¥‰Ω†{{ plantDays }}Â§©‰∫Ü,Èô™‰Ω†ÂÆåÊàê‰∫Ü{{ completedTasksCount }}‰∏™‰ªªÂä°
          </div>
        </div>
      </div>
    </div>
    
    <PlantDialog 
      :messages="plantStore.thoughts"
      :is-visible="showPlantThoughtDialog"
      :show-buttons="false"
      @primary-action="showPlantThoughtDialog = false"
    />
  </div>
</template>

<script>
import { ref, reactive, onMounted, computed, watch } from 'vue'
import { useCurrencyStore } from '../stores/currency'
import { usePlantStore } from '../stores/plant'
import { useTaskStore } from '../stores/task'
import { ElMessage } from 'element-plus'
import WeatherCanvas from '@/components/WeatherCanvas.vue'
import PlantDialog from '@/components/PlantDialog.vue'

// ÂØºÂÖ•Ê§çÁâ©ÂõæÁâá
import plant1Level1 from '@/assets/images/plant/1-1.png'
import plant1Level2 from '@/assets/images/plant/1-2.png'
import plant1Level3 from '@/assets/images/plant/1-3.png'
import plant2Level1 from '@/assets/images/plant/2-1.png'
import plant2Level2 from '@/assets/images/plant/2-2.png'
import plant2Level3 from '@/assets/images/plant/2-3.png'
import plant3Level1 from '@/assets/images/plant/3-1.png'
import plant3Level2 from '@/assets/images/plant/3-2.png'
import plant3Level3 from '@/assets/images/plant/3-3.png'
import plant4Level1 from '@/assets/images/plant/4-1.png'
import plant4Level2 from '@/assets/images/plant/4-2.png'
import plant4Level3 from '@/assets/images/plant/4-3.png'
import plant5Level1 from '@/assets/images/plant/5-1.png'
import plant5Level2 from '@/assets/images/plant/5-2.png'
import plant5Level3 from '@/assets/images/plant/5-3.png'

export default {
  name: 'GardenPage',
  components: {
    WeatherCanvas,
    PlantDialog
  },
  setup() {
    const currencyStore = useCurrencyStore()
    const plantStore = usePlantStore()
    const taskStore = useTaskStore()
    
    const selectedPlantForDialog = ref(null)
    const showPlantThoughtDialog = ref(false)
    const searchPlant = ref('')
    
    // Ê§çÁâ©ÂõæÁâáÊò†Â∞Ñ
    const plantImages = {
      'Áé´Áë∞': {
        1: plant1Level1,
        2: plant1Level2,
        3: plant1Level3
      },
      '‰ªô‰∫∫Êéå': {
        1: plant2Level1,
        2: plant2Level2,
        3: plant2Level3
      },
      'ÈÉÅÈáëÈ¶ô': {
        1: plant3Level1,
        2: plant3Level2,
        3: plant3Level3
      },
      'ÁôΩÁôæÂêà': {
        1: plant4Level1,
        2: plant4Level2,
        3: plant4Level3
      },
      'ÂêëÊó•Ëëµ': {
        1: plant5Level1,
        2: plant5Level2,
        3: plant5Level3
      }
    }
    
    // Ëé∑ÂèñÊ§çÁâ©ÂõæÁâá
    const getPlantImage = (plant) => {
      if (!plant || !plant.type) {
        return plant1Level1 // ËøîÂõûÈªòËÆ§ÂõæÁâá
      }

      const type = plant.type.trim() // ÁßªÈô§ÂèØËÉΩÂ≠òÂú®ÁöÑÂâçÂêéÁ©∫Ê†º
      const level = plant.level || 1
      
      // Ê£ÄÊü•Ê§çÁâ©Á±ªÂûãÂíåÁ≠âÁ∫ßÈôêÂà∂
      const clampLevel = Math.min(Math.max(level, 1), 3) // ÈôêÂà∂Á≠âÁ∫ßÂú®1-3‰πãÈó¥
      
      // Ê†πÊçÆÊ§çÁâ©Á±ªÂûãËøîÂõûÂØπÂ∫îÂõæÁâá
      const plantTypeImages = plantImages[type]
      if (!plantTypeImages) {
        return plant1Level1 // Â¶ÇÊûúÊâæ‰∏çÂà∞ÂØπÂ∫îÁ±ªÂûãÁöÑÂõæÁâáÔºåËøîÂõûÈªòËÆ§ÂõæÁâá
      }

      return plantTypeImages[clampLevel] || plant1Level1 // Â¶ÇÊûúÊâæ‰∏çÂà∞ÂØπÂ∫îÁ≠âÁ∫ßÁöÑÂõæÁâáÔºåËøîÂõûÈªòËÆ§ÂõæÁâá
    }
    
    // ÊâÄÊúâÊ§çÁâ©ÂàóË°® (Â∑≤Ëß£ÈîÅ)
    const allPlants = reactive([
      { id: 'plant1', name: 'ÁªØËâ≤ÁµÆËØ≠', type: 'Áé´Áë∞', emoji: 'üåπ', level: 1, experience: 0, weather: 'sunny', isMainPlant: false },
      { id: 'plant2', name: 'Ê≤ôÂ±øÊòüËäí', type: '‰ªô‰∫∫Êéå', emoji: 'üåµ', level: 1, experience: 0, weather: 'sunny', isMainPlant: false },
      { id: 'plant3', name: 'ÂÜ∞ÁàµÂ£´', type: 'ÈÉÅÈáëÈ¶ô', emoji: 'üå∏', level: 1, experience: 0, weather: 'sunny', isMainPlant: false },
      { id: 'plant4', name: '‰∫ëÂΩíÂ§Ñ', type: 'ÁôΩÁôæ‰Ωï', emoji: 'üå≤', level: 1, experience: 0, weather: 'sunny', isMainPlant: false },
      { id: 'plant5', name: 'Êó•ËΩÆÁ§ºËµû', type: 'ÂêëÊó•Ëëµ', emoji: 'üåª', level: 1, experience: 0, weather: 'sunny', isMainPlant: false }
    ])
    
    // Âä†ËΩΩÊ§çÁâ©ÂàóË°®
    onMounted(async () => {
      await plantStore.fetchPlants()
      
      // ÂàùÂßãÂåñÊ§çÁâ©Êï∞ÊçÆ
      for (const plant of allPlants) {
        // Ê£ÄÊü•ÊòØÂê¶Â∑≤Â≠òÂú®ËØ•Á±ªÂûãÁöÑÊ§çÁâ©
        const existingPlant = plantStore.plants.find(p => p.type === plant.type)
        if (!existingPlant) {
          try {
            const newPlant = await plantStore.createPlant({
              name: plant.name,
              type: plant.type,
              emoji: plant.emoji,
              isMainPlant: plant.isMainPlant
            })
            console.log('ÂàõÂª∫Êñ∞Ê§çÁâ©ÊàêÂäü:', newPlant)
          } catch (error) {
            console.error('ÂàõÂª∫Ê§çÁâ©Â§±Ë¥•:', error)
            ElMessage.error('ÂàõÂª∫Ê§çÁâ©Â§±Ë¥•')
          }
        }
      }
      
      // Â¶ÇÊûúÊ≤°Êúâ‰∏ªÊ§çÁâ©ÔºåÂ∞ÜÁ¨¨‰∏Ä‰∏™ËÆæ‰∏∫‰∏ªÊ§çÁâ©
      const mainPlant = plantStore.plants.find(p => p.isMainPlant)
      if (!mainPlant && plantStore.plants.length > 0) {
        const firstPlant = plantStore.plants[0]
        const plantId = firstPlant._id || firstPlant.id
        if (plantId) {
          try {
            await plantStore.updatePlant(plantId, { isMainPlant: true })
            console.log('ËÆæÁΩÆ‰∏ªÊ§çÁâ©ÊàêÂäü:', firstPlant.name)
          } catch (error) {
            console.error('ËÆæÁΩÆ‰∏ªÊ§çÁâ©Â§±Ë¥•:', error)
            ElMessage.error('ËÆæÁΩÆ‰∏ªÊ§çÁâ©Â§±Ë¥•')
          }
        }
      }
      
      await taskStore.fetchTasks()
    })
    
    // ËÆ°ÁÆóÊ§çÁâ©ÁªèÈ™åÁôæÂàÜÊØî
    const calculatePlantExp = (plant) => {
      const currentExp = plant.experience || 0
      const level = plant.level || 1
      return Math.min(100, (currentExp / (level * 100)) * 100)
    }
    
    // ÁªèÈ™åÊ†ºÂºèÂåñ
    const expFormat = (percentage) => {
      // ‰øÆÂ§çÔºöÈÄöËøápercentageÂèØ‰ª•ÊâæÂà∞ÂØπÂ∫îÁöÑÊ§çÁâ©Ôºå‰∏çÈúÄË¶Å‰æùËµñselectedPlantForDialog
      // Âú®ËøõÂ∫¶Êù°‰∏≠ÊòæÁ§∫Êó∂Ôºå‰ºö‰º†ÂÖ•ÂΩìÂâçÂÆûÈôÖÁöÑpercentage
      const plant = plantStore.plants.find(p => 
        calculatePlantExp(p) === percentage
      )
      
      // Â¶ÇÊûúÊâæ‰∏çÂà∞ÂåπÈÖçÁöÑÊ§çÁâ©ÔºåËøîÂõûÁôæÂàÜÊØî
      if (!plant) {
        // ÂõûÈÄÄÂà∞ÈÄöËøáselectedPlantForDialogÊü•ÊâæÊ§çÁâ©
        if (selectedPlantForDialog.value) {
          const fallbackPlant = plantStore.plants.find(p => 
            (p._id === selectedPlantForDialog.value._id) || 
            (p.id === selectedPlantForDialog.value.id)
          )
          if (fallbackPlant) {
            const currentExp = fallbackPlant.experience || 0
            const level = fallbackPlant.level || 1
            const nextLevelExp = level * 100
            return `${currentExp}/${nextLevelExp}`
          }
        }
        return `${Math.round(percentage)}%`
      }
      
      const currentExp = plant.experience || 0
      const level = plant.level || 1
      const nextLevelExp = level * 100
      return `${currentExp}/${nextLevelExp}`
    }
    
    // Êõ¥Êñ∞Ê§çÁâ©Â§©Ê∞î
    const updatePlantWeather = async (plant, weather) => {
      if (plant.weather === weather) return
      
      // Ëé∑ÂèñÊ≠£Á°ÆÁöÑÊ§çÁâ©ID
      const plantId = plant._id || plant.id
      if (!plantId) {
        console.error('Êó†Ê≥ïÊõ¥Êñ∞Ê§çÁâ©Â§©Ê∞î: Ê§çÁâ©IDÊó†Êïà', plant)
        ElMessage.error('Êõ¥Êñ∞Â§±Ë¥•ÔºöÊó†Ê≥ïËé∑ÂèñÊ§çÁâ©ID')
        return
      }
      
      console.log('Êõ¥Êñ∞Ê§çÁâ©Â§©Ê∞îÔºåÊ§çÁâ©ID:', plantId, 'Â§©Ê∞î:', weather)
      try {
        await plantStore.updatePlant(plantId, { weather })
        ElMessage.success('Ê§çÁâ©ÁéØÂ¢ÉÂ∑≤Êõ¥Êñ∞')
      } catch (error) {
        console.error('Êõ¥Êñ∞Ê§çÁâ©Â§©Ê∞îÂ§±Ë¥•:', error)
        ElMessage.error(`Êõ¥Êñ∞Â§±Ë¥•: ${error.message || 'Êú™Áü•ÈîôËØØ'}`)
      }
    }
    
    // ÊòæÁ§∫Ê§çÁâ©ÂøÉÂ£∞ÂØπËØùÊ°Ü
    const showDialog = async (plant) => {
      // Ëé∑ÂèñÊ≠£Á°ÆÁöÑÊ§çÁâ©ID
      const plantId = plant._id || plant.id
      if (!plantId) {
        console.error('Êó†Ê≥ïÊòæÁ§∫Ê§çÁâ©ÂøÉÂ£∞: Ê§çÁâ©IDÊó†Êïà', plant)
        ElMessage.error('Êó†Ê≥ïÊòæÁ§∫Ê§çÁâ©ÂøÉÂ£∞ÔºöÊ§çÁâ©IDÊó†Êïà')
        return
      }
      
      selectedPlantForDialog.value = plant
      showPlantThoughtDialog.value = true
      
      // Ëé∑ÂèñÊ§çÁâ©ÂøÉÂ£∞
      try {
        const thoughts = await plantStore.fetchPlantThoughts(plantId)
        // Â∞ÜÊ§çÁâ©ÂøÉÂ£∞ËΩ¨Êç¢‰∏∫Ê∂àÊÅØÊ†ºÂºè
        plantStore.thoughts = thoughts.map(thought => ({
          type: 'plant',
          content: thought.content,
          timestamp: thought.timestamp
        }))
      } catch (error) {
        console.error('Ëé∑ÂèñÊ§çÁâ©ÂøÉÂ£∞Â§±Ë¥•:', error)
        ElMessage.error('Ëé∑ÂèñÊ§çÁâ©ÂøÉÂ£∞Â§±Ë¥•')
      }
    }
    
    // ÁîüÊàêÊ§çÁâ©ÂøÉÂ£∞
    const generatePlantThought = async (plant) => {
      // Ëé∑ÂèñÊ≠£Á°ÆÁöÑÊ§çÁâ©ID
      const plantId = plant._id || plant.id
      if (!plantId) {
        console.error('Êó†Ê≥ïÁîüÊàêÊ§çÁâ©ÂøÉÂ£∞: Ê§çÁâ©IDÊó†Êïà', plant)
        ElMessage.error('ÁîüÊàêÂ§±Ë¥•ÔºöÊ§çÁâ©IDÊó†Êïà')
        return
      }
      
      try {
        const context = {
          weather: plant.weather || 'sunny',
          level: plant.level || 1,
          experience: plant.experience || 0,
          growthStage: plant.growthStage || 1
        }
        
        const thought = await plantStore.generatePlantThought(plantId, context)
        if (thought) {
          // Â∞ÜÊñ∞ÁîüÊàêÁöÑÊ§çÁâ©ÂøÉÂ£∞Ê∑ªÂä†Âà∞Ê∂àÊÅØÂàóË°®
          plantStore.thoughts.unshift({
            type: 'plant',
            content: thought.content,
            timestamp: thought.timestamp
          })
        }
        ElMessage.success('Ê§çÁâ©ÂøÉÂ£∞Â∑≤ÁîüÊàê')
      } catch (error) {
        console.error('ÁîüÊàêÊ§çÁâ©ÂøÉÂ£∞Â§±Ë¥•:', error)
        ElMessage.error(`ÁîüÊàêÂ§±Ë¥•: ${error.message || 'Êú™Áü•ÈîôËØØ'}`)
      }
    }
    
    // ËÆæÁΩÆ‰∏ªÊ§çÁâ©
    const setAsMainPlant = async (plant) => {
      if (plant.isMainPlant) return
      
      // Ëé∑ÂèñÊ≠£Á°ÆÁöÑÊ§çÁâ©ID
      const plantId = plant._id || plant.id
      if (!plantId) {
        console.error('Êó†Ê≥ïËÆæÁΩÆ‰∏ªÊ§çÁâ©: Ê§çÁâ©IDÊó†Êïà', plant)
        ElMessage.error('ËÆæÁΩÆÂ§±Ë¥•ÔºöÊó†Ê≥ïËé∑ÂèñÊ§çÁâ©ID')
        return
      }
      
      console.log('ËÆæÁΩÆ‰∏ªÊ§çÁâ©ÔºåÊ§çÁâ©ID:', plantId)
      try {
        // ÂÖàÂ∞ÜÊâÄÊúâÊ§çÁâ©ÁöÑ‰∏ªÊ§çÁâ©Áä∂ÊÄÅËÆæÁΩÆ‰∏∫false
        for (const p of plantStore.plants) {
          if (p.isMainPlant) {
            const prevMainPlantId = p._id || p.id
            await plantStore.updatePlant(prevMainPlantId, { isMainPlant: false })
          }
        }
        
        // ËÆæÁΩÆÊñ∞ÁöÑ‰∏ªÊ§çÁâ©
        await plantStore.updatePlant(plantId, { isMainPlant: true })
        
        // Êõ¥Êñ∞Ê§çÁâ©ÂøÉÂ£∞
        await plantStore.fetchPlantThoughts(plantId)
        
        // Â¶ÇÊûúÂΩìÂâçÊ≠£Âú®ÊòæÁ§∫Ê§çÁâ©ÂøÉÂ£∞ÂØπËØùÊ°ÜÔºåÊõ¥Êñ∞ÈÄâ‰∏≠ÁöÑÊ§çÁâ©
        if (showPlantThoughtDialog.value) {
          selectedPlantForDialog.value = plant
        }
        
        ElMessage.success('Â∑≤ËÆæÁΩÆ‰∏∫‰∏ªÊ§çÁâ©')
      } catch (error) {
        console.error('ËÆæÁΩÆ‰∏ªÊ§çÁâ©Â§±Ë¥•:', error)
        ElMessage.error(`ËÆæÁΩÆÂ§±Ë¥•: ${error.message || 'Êú™Áü•ÈîôËØØ'}`)
      }
    }
    
    // ËøáÊª§ÂêéÁöÑÊ§çÁâ©ÂàóË°®
    const filteredPlants = computed(() => {
      if (!searchPlant.value) return plantStore.plants
      
      const search = searchPlant.value.toLowerCase()
      return plantStore.plants.filter(plant => 
        plant.name.toLowerCase().includes(search)
      )
    })
    
    // ËÆ°ÁÆóÊ§çÁâ©Èô™‰º¥ÁöÑÂ§©Êï∞
    const plantDays = computed(() => {
      if (plantStore.plants.length === 0) return 0
      
      // ‰ΩøÁî®Á¨¨‰∏Ä‰∏™Ê§çÁâ©ÁöÑÂàõÂª∫Êó∂Èó¥ËÆ°ÁÆó
      const firstPlant = plantStore.plants[0]
      if (!firstPlant || !firstPlant.createdAt) return 0
      
      const createdDate = new Date(firstPlant.createdAt)
      const today = new Date()
      const diffTime = Math.abs(today - createdDate)
      const diffDays = Math.ceil(diffTime / (1000 * 60 * 60 * 24))
      
      return diffDays
    })
    
    // Ëé∑ÂèñÂÆåÊàêÁöÑ‰ªªÂä°Êï∞Èáè
    const completedTasksCount = computed(() => {
      return taskStore.completedTasks.length
    })
    
    // ÁõëÂê¨‰∏ªÊ§çÁâ©ÁöÑÂèòÂåñ
    watch(() => plantStore.plants.find(p => p.isMainPlant), async (newMainPlant) => {
      if (newMainPlant) {
        // Êõ¥Êñ∞ÈÄâ‰∏≠ÁöÑÊ§çÁâ©
        selectedPlantForDialog.value = newMainPlant
        
        // Â¶ÇÊûúÂØπËØùÊ°ÜÊòØÊâìÂºÄÁöÑÔºåÊõ¥Êñ∞Ê§çÁâ©ÂøÉÂ£∞
        if (showPlantThoughtDialog.value) {
          const plantId = newMainPlant._id || newMainPlant.id
          if (plantId) {
            try {
              await plantStore.fetchPlantThoughts(plantId)
            } catch (error) {
              console.error('Êõ¥Êñ∞Ê§çÁâ©ÂøÉÂ£∞Â§±Ë¥•:', error)
              ElMessage.error('Êõ¥Êñ∞Ê§çÁâ©ÂøÉÂ£∞Â§±Ë¥•')
            }
          }
        }
      }
    }, { immediate: true })
    
    return {
      currencyStore,
      plantStore,
      taskStore,
      selectedPlantForDialog,
      showPlantThoughtDialog,
      searchPlant,
      myPlants: computed(() => plantStore.plants),
      filteredPlants,
      plantDays,
      completedTasksCount,
      
      // ÊñπÊ≥ï
      calculatePlantExp,
      expFormat,
      updatePlantWeather,
      showDialog,
      generatePlantThought,
      setAsMainPlant,
      getPlantImage
    }
  }
}
</script>

